.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
   margin-bottom: 100px;
 
}
.new-collections h1{
    color: #171717;
    font-size:50px ;
    font-weight: 600;
   
}
.new-collections hr{
    width: 300px;
    padding: 2px;
    border-radius: 80%;
    background: #252525; ;
}
.collections{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 20px;
    margin-top: 50px;
}



@media (max-width:1280px){
    .new-collections{
        margin-top: -70px;
    
    }
    .new-collections h1{
        font-size:40px ;
    }
    .new-collections hr{
        width: 250px;
       
    }
    .new-collections-item{
        margin-top: 30px;
        gap: 20px;
    }
}

@media (max-width:1024px){
    .new-collections{
        margin-top: 20px;
       
    }
    .new-collections h1{
        font-size:30px ;
    }
    .new-collections hr{
        width: 200px;
       
    }
    .new-collections-item{
        margin-top: 20px;
        gap: 15px;
    }
}
@media (max-width:800px){
    .new-collections{
        gap: 2px;
        margin-top: 0px;
        margin-bottom: 120px;
    }
    .new-collections h1{
        font-size:30px ;
    }
    .new-collections hr{
        width: 200px;
       
    }
    .collections{
        gap:15px;
        display: grid;
        grid-template-columns: 1fr 1fr;
      
    }
}
@media (max-width:500px){
    .new-collections{
       
       gap: 5px;
       
    }
    .new-collections h1{
        font-size:22px ;
    }
    .new-collections hr{
        width: 130px;
        margin-bottom:-20px;
        gap: 20px;
    }
}